import io from "socket.io-client";

const socket = io.connect("worldofcalculators.com", {
  path: "/my-servers/voice-chat/"
});

// const socket = io.connect("localhost:8000", {
//   path: "/"
// });

const socketio = {
  install(Vue) {
    Vue.mixin({
      created() {
        this.$socket = socket;
      }
    });
  }
};

export default socketio;
