<template>
  <div class="dashboard">
    <v-row class="mt-8">
      <v-col class="d-flex align-center justify-center">
        <v-card loading shaped elevation="3" class="mx-auto">
          <v-card-title class="d-flex justify-center">
            Welcome {{ username }}
          </v-card-title>
          <v-card-subtitle
            >You can wait here for a call, or type your friend's name and call
            em.
          </v-card-subtitle>
          <v-card-text>
            <v-form autocomplete="off" ref="form" @submit.prevent="call">
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="friendname"
                      label="friend's username"
                      :rules="rules"
                      required
                    ></v-text-field>
                    <v-text-field
                      v-model="secretmessage"
                      label="Enter something so your friend knows it's you."
                      :rules="rules"
                      required
                    ></v-text-field>
                    <p v-if="callError" class="text-center red--text">
                      {{ callError }}
                    </p>
                  </v-col>
                  <v-col cols="12" class="d-flex justify-center">
                    <v-btn
                      :disabled="loading"
                      :loading="loading"
                      type="submit"
                      color="secondary"
                    >
                      Call
                      <v-icon dark right>mdi-phone</v-icon>
                    </v-btn>
                  </v-col>
                  <v-col>
                    <v-list subheader v-if="missedCalls.length > 0">
                      <v-subheader>Missed Calls</v-subheader>
                      <v-list-item
                        v-for="(missedCall, index) in missedCalls"
                        :key="index"
                        @click="callMissedFriend(missedCall)"
                      >
                        <v-list-item-avatar :color="randomColor()">
                          <p></p>
                        </v-list-item-avatar>

                        <v-list-item-content>
                          <v-list-item-title
                            v-text="missedCall.friendname"
                          ></v-list-item-title>
                        </v-list-item-content>

                        <v-list-item-icon>
                          {{ missedCall.count }}
                        </v-list-item-icon>
                      </v-list-item>
                    </v-list>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <!-- The calling overlay -->
    <CallOverlay
      v-if="showCallOverlay"
      @callRejected="showCallRejected"
      @closeOverlay="closeOverlay"
      @callTimeout="showCallTimeout"
    />
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import CallOverlay from "@/components/CallOverlay";

export default {
  name: "Dashboard",
  components: {
    CallOverlay
  },
  data() {
    return {
      rules: [value => !!value || "Required."],
      loading: false,
      friendname: null,
      secretmessage: null,
      showCallOverlay: false,
      callError: null
    };
  },
  created() {
    setInterval(() => {
      this.$socket.emit("user-check-connection", { username: this.username });
    }, 1000);

    this.$socket.on("user-check-connection-success", () => {
      //Cool, we will do Nothing.
    });

    this.$socket.on("user-check-connection-error", () => {
      this.$router.push("/");
    });
  },
  destroyed() {
    this.$socket.off("user-check-connection-success");
    this.$socket.off("user-check-connection-error");
    this.$socket.off("call-issue-success");
    this.$socket.off("call-issue-error");
  },
  methods: {
    ...mapMutations(["setFriendname", "setSecretmessage", "removeMissedCall"]),
    call() {
      this.callError = null;
      this.loading = true;
      let validationResult = this.$refs.form.validate();
      if (validationResult) {
        if (this.username == this.friendname) {
          this.callError = "You can't call yourself";
          this.loading = false;
          return;
        }
        this.setFriendname(this.friendname);
        this.setSecretmessage(this.secretmessage);

        this.makeCall();
      } else {
        this.loading = false;
      }
    },
    callMissedFriend(missedCall) {
      this.callError = null;
      this.setFriendname(missedCall.friendname);
      this.friendname = missedCall.friendname;
      this.setSecretmessage(" ");
      this.removeMissedCall(missedCall);
      this.makeCall();
    },
    makeCall() {
      /* make the call */
      this.$socket.emit("call-issue", {
        username: this.username,
        friendname: this.friendname,
        secretmessage: this.secretmessage
      });

      this.$socket.on("call-issue-success", () => {
        /* On successful call setup, show the ringing overlay. */
        this.loading = false;
        this.showCallOverlay = true;
      });

      this.$socket.on("call-issue-error", data => {
        this.loading = false;
        this.callError = data.error;
      });
    },
    closeOverlay() {
      this.showCallOverlay = false;
    },
    showCallRejected() {
      this.closeOverlay();
      this.callError = "Sorry, user is busy now.";
    },
    showCallTimeout() {
      this.closeOverlay();
      this.callError = "Call Timeout, your friend has been notified.";
    },
    randomColor() {
      const randomColors = [
        "grey",
        "orange",
        "red",
        "blue",
        "purple",
        "violet",
        "pink"
      ];
      return randomColors[Math.floor(Math.random() * 7)];
    }
  },
  computed: {
    ...mapState(["username", "missedCalls"])
  }
};
</script>

<style scoped></style>
