import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Dashboard from "../views/Dashboard.vue";
import CallRunning from "../views/CallRunning.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard,
    beforeEnter: (to, from, next) => {
      if (!from.name) {
        next({ name: "Home" });
      } else {
        next();
      }
    }
  },
  {
    path: "/call",
    name: "CallRunning",
    component: CallRunning,
    beforeEnter: (to, from, next) => {
      if (!from.name) {
        next({ name: "Home" });
      } else {
        next();
      }
    }
  }
];

const router = new VueRouter({
  // mode: "history",
  base: "/simple-voice-call/",
  routes
});

export default router;
