class Event {
  eventsObj = {};

  on(event, handler) {
    if (this.eventsObj[event]) {
      this.eventsObj[event].push(handler);
    } else {
      this.eventsObj[event] = [handler];
    }
  }

  off(event) {
    delete this.eventsObj[event];
  }

  emit(event, data) {
    if (this.eventsObj[event]) {
      this.eventsObj[event].forEach(e => {
        e(data);
      });
    }
  }
}

export default Event;
