import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    username: "",
    friendname: "",
    secretmessage: "",
    incomingsecret: "",
    inCall: false,
    initiator: false,
    missedCalls: []
  },
  mutations: {
    setUsername(state, username) {
      state.username = username;
    },
    setFriendname(state, friendname) {
      state.friendname = friendname;
    },
    setSecretmessage(state, secretmessage) {
      state.secretmessage = secretmessage;
    },
    setIncomingSecret(state, incomingsecret) {
      state.incomingsecret = incomingsecret;
    },
    setInCall(state, inCall) {
      state.inCall = inCall;
    },
    setInitiator(state, initiator) {
      state.initiator = initiator;
    },
    setMissedCalls(state, friendname) {
      const result = state.missedCalls.find(
        call => call.friendname == friendname
      );
      if (!result) {
        state.missedCalls.push({
          friendname,
          count: 1,
          initial: friendname[0].toUpperCase()
        });
      } else {
        result.count++;
      }
    },
    removeMissedCall(state, missedCall) {
      const index = state.missedCalls.findIndex(
        call => call.friendname === missedCall.friendname
      );
      state.missedCalls.splice(index, 1);
    }
  },
  actions: {},
  modules: {}
});
