<template>
  <div class="call-overlay">
    <v-overlay absolute opacity="0.9" z-index="999">
      <v-row>
        <v-col cols="12" class="d-flex justify-center">
          <h2>Calling {{ friendname }}</h2>
        </v-col>
        <v-col class="d-flex justify-space-around">
          <v-btn @click="cancelCall()">Cancel Call</v-btn>
        </v-col>
      </v-row>
    </v-overlay>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import incomingTone from "@/assets/sounds/tele_ring.mp3";

export default {
  name: "CallOverlay",
  data() {
    return {
      callingToneAudio: null,
      startTime: null,
      startCallTimer: null
    };
  },
  created() {
    this.startTime = Date.now();

    this.startCallTimer = setInterval(() => {
      if (Date.now() - this.startTime >= 60000) {
        this.cancelCall();
        this.$emit("callTimeout");
      }
    }, 1000);

    /* Set the In Call mutation. */
    this.setInCall(true);

    this.playRingtone();

    /* Wait for the user reply. Once he replies */
    this.$socket.on("call-accepted", () => {
      clearInterval(this.startCallTimer);
      this.stopRingtone();
      this.setInitiator(true);
      this.$router.push("call");
    });

    this.$socket.on("call-rejected", () => {
      clearInterval(this.startCallTimer);
      this.stopRingtone();
      this.setInCall(false);
      this.$emit("callRejected");
    });
  },
  destroyed() {
    this.$socket.off("call-accepted");
    this.$socket.off("call-rejected");
  },
  methods: {
    ...mapMutations(["setInCall", "setInitiator"]),
    playRingtone() {
      this.callingToneAudio = new Audio(incomingTone);
      this.callingToneAudio.play();
      this.callingToneAudio.loop = true;
    },
    stopRingtone() {
      this.callingToneAudio.pause();
      this.callingToneAudio.currentTime = 0;
    },
    closeOverlay() {
      this.stopRingtone();
      this.$emit("closeOverlay");
    },
    cancelCall() {
      clearInterval(this.startCallTimer);
      this.closeOverlay();
      this.setInCall(false);
      this.$socket.emit("call-cancel", {
        username: this.username,
        friendname: this.friendname
      });
    }
  },
  computed: {
    ...mapState(["username", "friendname"])
  }
};
</script>

<style scoped></style>
