import adapter from "webrtc-adapter"; // eslint-disable-line no-unused-vars
import Event from "./eventService.js";

class WebRTCService extends Event {
  peerConnection = null;
  mediaType = null;
  mediaConstraints = null;
  iceServers = null;
  signalingChannel = null;
  localStream = null;

  /*
   * Params:
   *   mediaType: Object (audio, video).
   *   iceServers: Object (iceServers)
   *   signalingChannel: Object (contains the send message method and on message event)
   */
  constructor(mediaType, iceServers, signalingChannel) {
    super();
    this.mediaType = mediaType;
    this.iceServers = iceServers;
    this.signalingChannel = signalingChannel;
  }

  async initiateCall(initiator) {
    if (initiator) {
      this.createPeerConnection();
      await this.getUserMedia();
      this.attatchTracksToPeerConnection();
    }
    this.signalingChannel.on("handshake", this.handleMessages.bind(this));
  }

  createPeerConnection() {
    if (!this.peerConnection) {
      this.peerConnection = new RTCPeerConnection({
        iceServers: this.iceServers
      });
      this.peerConnection.onconnectionstatechange = this.handleOnConnectionStateChange.bind(
        this
      );
      this.peerConnection.onicecandidate = this.handleOnIceCandidate.bind(this);
      this.peerConnection.oniceconnectionstatechange = this.handleOnIceConnectionStateChange.bind(
        this
      );
      this.peerConnection.onicegatheringstatechange = this.handleOnIceGatheringStateChange.bind(
        this
      );
      this.peerConnection.onnegotiationneeded = this.handleOnNegotiationNeeded.bind(
        this
      );
      this.peerConnection.ontrack = this.handleOnTrack.bind(this);
    }
  }

  createMediaConstraints() {
    const mediaConstraints = {};
    if (this.mediaType.audio) {
      mediaConstraints.audio = {
        sampleRate: 8000,
        sampleSize: 8,
        volume: 1,
        echoCancellation: true,
        noiseSuppression: true
      };
    }
    if (this.mediaType.video) {
      /* @TODO: Implement this feature */
    }
    return (this.mediaConstraints = mediaConstraints);
  }

  async getUserMedia() {
    try {
      this.localStream = await navigator.mediaDevices.getUserMedia(
        this.mediaConstraints || this.createMediaConstraints()
      );
    } catch (e) {
      this.handleGetUserMediaErrors(e);
    }
  }

  attatchTracksToPeerConnection() {
    try {
      this.localStream.getTracks().forEach(track => {
        this.peerConnection.addTrack(track, this.localStream);
      });
    } catch (e) {
      this.handleGetUserMediaErrors(e);
    }
  }

  handleGetUserMediaErrors(e) {
    switch (e.name) {
      case "NotFoundError":
        alert("No Camera/Microphones are found");
        break;
      case "SecurityError":
      case "PermissionDeniedError":
        alert("Please allow your camera/Microphone and try again.");
        break;
      default:
        alert("Error opening your camera and/or microphone: " + e.message);
        break;
    }
    this.closeCall();
  }

  closeCall() {
    if (this.peerConnection) {
      this.peerConnection.onconnectionstatechange = null;
      this.peerConnection.onicecandidate = null;
      this.peerConnection.oniceconnectionstatechange = null;
      this.peerConnection.onicegatheringstatechange = null;
      this.peerConnection.onnegotiationneeded = null;
      this.peerConnection.ontrack = null;
      this.peerConnection.close();
      this.peerConnection = null;
    }
    this.emit("close-call");
  }

  async sendOffer(restart) {
    try {
      const offer = await this.peerConnection.createOffer({
        iceRestart: restart ? true : false
      });

      if (this.peerConnection.signalingState != "stable") {
        return;
      }

      await this.peerConnection.setLocalDescription(offer);

      this.signalingChannel.send({
        type: "offer",
        sdp: this.peerConnection.localDescription
      });
    } catch (e) {
      this.handleErrors(e);
    }
  }

  handleOnNegotiationNeeded() {
    this.sendOffer(false);
  }

  async handleIncomingOffer(data) {
    try {
      /* Creates one if not created */
      this.createPeerConnection();

      const offer = new RTCSessionDescription(data.sdp);

      if (this.peerConnection.signalingState != "stable") {
        await Promise.all([
          this.peerConnection.setLocalDescription({ type: "rollback" }),
          this.peerConnection.setRemoteDescription(offer)
        ]);
        return;
      } else {
        await this.peerConnection.setRemoteDescription(offer);
      }

      if (!this.localStream) {
        await this.getUserMedia();
        this.attatchTracksToPeerConnection();
      }

      const answer = await this.peerConnection.createAnswer();
      await this.peerConnection.setLocalDescription(answer);

      this.signalingChannel.send({
        type: "answer",
        sdp: this.peerConnection.localDescription
      });
    } catch (e) {
      this.handleErrors(e);
    }
  }

  async handleIncomingAnswer(data) {
    try {
      const answer = new RTCSessionDescription(data.sdp);
      await this.peerConnection.setRemoteDescription(answer);
    } catch (e) {
      this.handleErrors(e);
    }
  }

  handleOnIceCandidate(e) {
    if (e.candidate) {
      this.signalingChannel.send({
        type: "new-ice-candidate",
        candidate: e.candidate
      });
    }
  }

  async handleIncomingIceCandidate(data) {
    const candidate = new RTCIceCandidate(data.candidate);
    try {
      await this.peerConnection.addIceCandidate(candidate);
    } catch (e) {
      this.handleErrors(e);
    }
  }

  handleOnTrack(e) {
    const stream = e.streams[0];
    this.emit("new-remote-stream", { stream });
  }

  /* Not supported by everybrowser.. fallback to IceGatheringStateChange */
  handleOnConnectionStateChange() {
    switch (this.peerConnection.connectionState) {
      case "connected":
        this.emit("call-connected");
        break;
      case "new":
      case "connecting":
        this.emit("call-connecting");
        break;
      case "disconnected":
      case "failed":
        break;
      case "closed":
        this.closeCall();
        break;
    }
  }

  handleOnIceGatheringStateChange() {
    switch (this.peerConnection.iceGatheringState) {
      case "new":
      case "complete":
        this.emit("ice-gathering-idle");
        break;
      case "gathering":
        this.emit("ice-gathering-active");
        break;
    }
  }

  async restartIce() {
    this.sendOffer(true);
  }

  handleOnIceConnectionStateChange() {
    if (
      this.peerConnection.iceConnectionState === "failed" ||
      this.peerConnection.iceConnectionState === "disconnected" ||
      this.peerConnection.iceConnectionState === "closed"
    ) {
      this.restartIce();
    }
  }

  handleMessages(data) {
    switch (data.type) {
      case "offer":
        this.handleIncomingOffer(data);
        break;
      case "answer":
        this.handleIncomingAnswer(data);
        break;
      case "new-ice-candidate":
        this.handleIncomingIceCandidate(data);
        break;
    }
  }

  muteAudio(isMute) {
    try {
      if (isMute) {
        this.localStream.getAudioTracks()[0].enabled = false;
      } else {
        this.localStream.getAudioTracks()[0].enabled = true;
      }
    } catch (e) {
      this.handleErrors(e);
    }
  }

  handleErrors(e) {
    alert(e);
  }
}

export default WebRTCService;
