<template>
  <v-app>
    <v-app-bar elevation="0" app color="primary" dark>
      <v-row>
        <v-col class="d-flex justify-center">
          <v-toolbar-title>Simple voice call App</v-toolbar-title>
        </v-col>
      </v-row>
    </v-app-bar>

    <v-content style="position: relative">
      <v-container fluid>
        <router-view></router-view>
        <v-btn
          @click="logout"
          v-if="$route.path != '/'"
          color="pink"
          title="logout"
          dark
          small
          absolute
          bottom
          right
          fab
        >
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </v-container>
    </v-content>

    <v-footer app color="primary" dark>
      <v-card-text class="d-flex justify-center">
        {{ new Date().getFullYear() }} — <strong>Simple Voice Call App</strong>
      </v-card-text>
    </v-footer>

    <IncomingCallOverlay @closeOverlay="closeOverlay" v-if="incomingCall" />
  </v-app>
</template>

<script>
import IncomingCallOverlay from "@/components/IncomingCallOverlay";
import { mapState, mapMutations } from "vuex";

export default {
  name: "App",
  components: {
    IncomingCallOverlay
  },
  data: () => ({
    incomingCall: false
  }),
  created() {
    this.$socket.on("incoming-call", data => {
      if (!this.inCall) {
        this.setFriendname(data.username);
        this.setIncomingSecret(data.secretmessage);
        this.incomingCall = true;
      } else {
        this.$socket.emit("call-reject", {
          username: this.username,
          friendname: data.username
        });
      }
    });
  },
  methods: {
    ...mapMutations(["setFriendname", "setIncomingSecret", "setUsername"]),
    closeOverlay() {
      this.incomingCall = false;
    },
    logout() {
      localStorage.removeItem("username");
      this.setUsername(null);
      this.$socket.emit("user-logout");
      this.$router.push("/");
    }
  },
  computed: {
    ...mapState(["inCall", "username"])
  }
};
</script>

<style>
/* For the theme background color */
.v-application {
  background-color: var(--v-background-base) !important;
}
</style>
