<template>
  <div class="call-running">
    <v-row class="mt-8">
      <v-col class="d-flex align-center justify-center">
        <v-card shaped elevation="3" class="mx-auto">
          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  class="d-flex justify-space-between align-center"
                >
                  <v-avatar color="indigo" size="80">{{
                    username[0].toUpperCase()
                  }}</v-avatar>
                  <v-avatar color="green" size="80">{{
                    friendname[0].toUpperCase()
                  }}</v-avatar>
                </v-col>
                <v-col cols="12" class="d-flex justify-center">
                  <div class="time-passed">
                    <p class="time-passed--time">{{ timePassed }}</p>
                  </div>
                </v-col>
                <v-col cols="12" class="d-flex justify-center" color="info">
                  <div class="connection-status">
                    <p>{{ connectionStatus }}</p>
                  </div>
                </v-col>
                <v-col cols="12">
                  <v-divider class="mt-3"></v-divider>
                </v-col>
                <v-col cols="12" class="d-flex justify-space-around">
                  <v-btn ref="mute" @click="mute" icon color="secondary">
                    <v-icon dark right title="unmute" v-if="isMute"
                      >mdi-microphone-off</v-icon
                    >
                    <v-icon dark right title="mute" v-else
                      >mdi-microphone</v-icon
                    >
                  </v-btn>
                  <v-btn icon title="hangup" color="secondary" @click="hangup">
                    <v-icon dark right>mdi-phone-hangup</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <audio ref="audio" v-show="false" autoplay></audio>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import WebRTCService from "@/services/webRTCService";

export default {
  name: "CallRunning",
  created() {
    this.webRTC = new WebRTCService(
      { audio: true },
      this.iceServers,
      this.signalingChannel()
    );
    this.webRTC.initiateCall(this.initiator);

    this.webRTC.on("close-call", () => {
      this.stopAudio();
      this.$router.push("dashboard");
    });

    this.webRTC.on("new-remote-stream", data => {
      const stream = data.stream;
      this.$refs.audio.srcObject = stream;
    });

    this.webRTC.on("call-connected", () => {
      this.startCountingTime();
      this.connectionStatus = "Connected";
    });

    this.webRTC.on("ice-gathering-idle", () => {
      this.connectionStatus = "Connected";
    });

    this.webRTC.on("call-connecting", () => {
      this.connectionStatus = "connecting...";
    });

    this.webRTC.on("ice-gathering-active", () => {
      this.connectionStatus = "routing connection...";
    });

    /* Socket Listeners */
    this.$socket.on("call-hungup", () => {
      this.stopAudio();
      this.setInCall(false);
      this.$router.push("dashboard");
    });
  },
  destroyed() {
    this.$socket.off("handshake");
    this.$socket.off("call-hungup");
    this.webRTC.off("close-call");
    this.webRTC.off("new-remote-stream");
    this.webRTC.off("call-connected");
    this.webRTC.off("ice-gathering-idle");
    this.webRTC.off("call-connecting");
    this.webRTC.off("ice-gathering-active");
  },
  data() {
    return {
      callStart: null,
      tpassed: 0,
      isMute: false,
      peerConnection: null,
      connectionStatus: null,
      iceServers: [
        {
          urls: "stun:stun.l.google.com:19302"
        },
        {
          urls: "turn:13.250.13.83:3478?transport=udp",
          credential: "YzYNCouZM1mhqhmseWk6",
          username: "YzYNCouZM1mhqhmseWk6"
        },
        {
          urls: "turn:my-turn-server.mycompany.com:19403",
          username: "optional-username",
          credential: "auth-token"
        },
        {
          urls: "turn:numb.viagenie.ca",
          credential: "muazkh",
          username: "webrtc@live.com"
        },
        {
          urls: "turn:192.158.29.39:3478?transport=udp",
          credential: "JZEOEt2V3Qb0y27GRntt2u2PAYA=",
          username: "28224511:1379330808"
        },
        {
          urls: "turn:192.158.29.39:3478?transport=tcp",
          credential: "JZEOEt2V3Qb0y27GRntt2u2PAYA=",
          username: "28224511:1379330808"
        },
        {
          urls: "turn:turn.bistri.com:80",
          credential: "homeo",
          username: "homeo"
        },
        {
          urls: "turn:turn.anyfirewall.com:443?transport=tcp",
          credential: "webrtc",
          username: "webrtc"
        }
      ]
    };
  },
  methods: {
    ...mapMutations(["setInCall"]),
    signalingChannel() {
      const send = data => {
        this.$socket.emit("peer-data", {
          friendname: this.friendname,
          username: this.username,
          ...data
        });
      };
      const on = (event, cb) => {
        this.$socket.on(event, cb);
      };
      return {
        send,
        on
      };
    },
    mute() {
      this.isMute = !this.isMute;
      this.webRTC.muteAudio(this.isMute);
    },
    hangup() {
      this.$socket.emit("call-hangup", {
        username: this.username,
        friendname: this.friendname
      });
      this.webRTC.closeCall();
      this.setInCall(false);
    },
    stopAudio() {
      if (
        this.$refs.audio.srcObject &&
        this.$refs.audio.srcObject.getTracks()
      ) {
        this.$refs.audio.srcObject.getTracks().forEach(track => track.stop());
      }
    },
    startCountingTime() {
      if (!this.callStart) {
        this.callStart = Date.now();
        setInterval(() => {
          this.tpassed = Date.now() - this.callStart;
        }, 100);
      }
    }
  },
  computed: {
    ...mapState(["username", "friendname", "initiator"]),
    timePassed() {
      const hours = parseInt(this.tpassed / 3600000);
      const minutes = parseInt(this.tpassed / 60000) - hours * 60;
      const seconds =
        parseInt(this.tpassed / 1000) - minutes * 60 - hours * 3600;
      return `${hours ? hours.toString().padStart(2, "0") + ":" : ""}${
        minutes ? minutes.toString().padStart(2, "0") + ":" : ""
      }${seconds.toString().padStart(2, "0")}`;
    }
  }
};
</script>

<style scoped>
.time-passed--time {
  font-size: 2rem;
}
</style>
