<template>
  <div class="user-select">
    <v-row class="mt-8">
      <v-col class="d-flex align-center justify-center">
        <v-card shaped elevation="3" class="mx-auto">
          <v-card-title class="d-flex justify-center">Who are you</v-card-title>
          <v-card-text>
            <v-form autocomplete="off" ref="form" @submit.prevent="connect">
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="username"
                      :rules="rules"
                      label="username"
                      required
                    ></v-text-field>
                    <p v-if="connectionError" class="text-center red--text">
                      {{ connectionError }}
                    </p>
                  </v-col>
                  <v-col cols="12" class="d-flex justify-center">
                    <v-btn :loading="loading" type="submit" color="secondary"
                      >Connect</v-btn
                    >
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "UserSelect",
  mounted() {
    const username = localStorage.getItem("username");
    if (username && username.length <= 30 && username.length >= 2) {
      this.username = username;
      this.setUsername(this.username);
      this.submitUsername();
    }
  },
  data() {
    return {
      username: null,
      error: null,
      rules: [
        value => !!value || "Required.",
        value => (value || "").length <= 30 || "Max 30 characters",
        value => (value || "").length >= 2 || "Min 2 characters"
      ],
      loading: false,
      connectionError: null
    };
  },
  methods: {
    ...mapMutations(["setUsername"]),
    destroyed() {
      this.$socket.off("user-connect-error");
      this.$socket.off("user-connect-success");
    },
    connect() {
      this.loading = true;
      this.connectionError = null;
      let validationResult = this.$refs.form.validate();
      if (validationResult) {
        this.setUsername(this.username);
        this.submitUsername();
      } else {
        this.loading = false;
      }
    },
    submitUsername() {
      /* Connect to socket */
      this.$socket.emit("user-connect", { username: this.username });

      this.$socket.on("user-connect-error", data => {
        this.connectionError = data.error;
        this.loading = false;
      });

      this.$socket.on("user-connect-success", () => {
        localStorage.setItem("username", this.username);
        this.$router.push("dashboard").catch(() => {});
      });
    }
  }
};
</script>

<style scoped></style>
